.thumb {
    width: 100%;
    height: 225px;
    position: relative;
    margin-bottom: 20px;
    border-radius: 10px;
    animation: fadeInUp 600ms ease-in-out;
    overflow: hidden;

    &__img {
        width: inherit;
        height: inherit;
        object-fit: cover;
        border-radius: inherit;
        transform: scale(1);
        transform: transform 300ms;
    }

    &__title {
        position: absolute;
        bottom: 0;
        font-size: 18px;
        font-weight: 500;
        margin: 15px 20px;
        color: $white;
        box-sizing: inherit;
    }

    &__overlay {
        position: absolute;
        top: 0;
        background: linear-gradient(transparent, rgba(0, 0, 0, 0.5));
        height: 100%;
        width: 100%;
        border-radius: inherit;
    }

    &:hover {
        > .thumb__img {
            transform: scale(1.01);
        }
    }
}
h2.thumb__title {
    display: flex;
    align-items: flex-end;
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(5px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}